*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

body {
  height: 100vh;
  background-color: #e8eaef;
  padding-top: 44px;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 8px;
  background: #f2f3f6;
  box-shadow: 3px 9px 9px 0px rgba(199, 199, 199, 0.45);
  z-index: 10;
}
.header img {
  max-height: 20vw;
  padding-right: 4px;
}
.header h1 {
  font-size: 0.8rem;
  text-transform: uppercase;
  font-family: "VT323", monospace;
}

.news {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 8px;
}
.news .news-item {
  display: flex;
  flex-direction: column;
  gap: 8px;
  background: #f2f3f6;
  margin-bottom: 12px;
  padding: 12px;
  box-shadow: 3px 9px 9px 0px rgba(199, 199, 199, 0.45);
  border-radius: 8px;
  overflow: hidden;
  width: 100%;
  word-wrap: break-word;
  overflow-wrap: break-word;
}
.news .news-item .card-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 0.6rem;
  font-weight: bold;
  gap: 8px;
}
.news .news-item .card-header .score,
.news .news-item .card-header .type {
  width: 100%;
  padding: 4px 8px;
  border-radius: 8px;
  border: 1px solid rgb(8, 8, 26);
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  align-items: center;
}
.news .news-item .date {
  font-size: 0.7rem;
  color: gray;
}
.news .news-item .title {
  font-weight: 600;
  font-size: 1rem;
}
.news .news-item .author {
  color: gray;
  font-size: 0.7rem;
}
.news .news-item .card-footer .link-btn {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: rgb(124, 124, 124);
  border: none;
  box-shadow: 3px 9px 9px 0px rgba(199, 199, 199, 0.45);
  border-radius: 8px;
  padding: 4px 8px;
  flex: 1;
  text-transform: uppercase;
}
.news .news-item .card-footer .link-btn a {
  font-size: 0.8rem;
  text-decoration: none;
  color: white;
  font-weight: bold;
}
.news .news-item .card-footer .link-btn img {
  height: 16px;
  filter: invert(100%) brightness(200%);
}

.container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.container .btn {
  margin: 8px;
  margin-top: -8px;
  text-transform: uppercase;
  font-weight: 600;
  background: black;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 16px 20px;
  border-radius: 16px;
  border: none;
